import React, { useState } from "react";
import { useDispatch } from "react-redux";
import SimpleAlerts from "../../components/messageAlert/Alert";

export default function LoginForm() {
  const dispatch = useDispatch();
  const [userDetails, setuserDetails] = useState({
    email: "",
    password: "",
  });
  const handleChange = (value, attr) => {
    setuserDetails({
      ...userDetails,
      [attr]: value,
    });
  };
  const [message, setmessage] = useState({
    status: "",
    message: "",
    loader: false,
  });
  const handleSubmit = (e) => {
    e.preventDefault();
    setmessage({
      ...message,
      loader: true,
    });
    const requestOptions = {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        email: userDetails.email,
        password: userDetails.password,
      }),
    };
    fetch(`${process.env.REACT_APP_URL}/user/login`, requestOptions).then(
      (res) => {
        res.json().then((result) => {
          if (result.success) {
            dispatch({ type: "user_details", data: result.message });
            window.location.href = "/";
          } else {
            setmessage({
              message: result.message,
              status: "error",
              loader: false,
            });
          }
        });
      }
    );
  };
  return (
    <div>
      <form>
        <div class="login-form">
          <h4 class="login-title">Returning Customer</h4>
          <p>
            <span class="font-weight-bold">I am a returning customer</span>
          </p>
          {message.message ? <SimpleAlerts details={{ ...message }} /> : ""}
          <div class="row">
            <div class="col-md-12 col-12 mb--15">
              <label for="email">Enter your email address here...</label>
              <input
                class="mb-0 form-control"
                type="email"
                id="email1"
                placeholder="Enter you email address here..."
                onChange={(evt) => handleChange(evt.target.value, "email")}
              />
            </div>
            <div class="col-12 mb--20">
              <label for="password">Password</label>
              <input
                class="mb-0 form-control"
                type="password"
                id="login-password"
                placeholder="Enter your password"
                onChange={(evt) => handleChange(evt.target.value, "password")}
              />
            </div>
            <div class="col-md-12">
              {message.loader ? (
                <a href="" class="btn btn-outlined">
                  Please wait...
                </a>
              ) : (
                <a href="" class="btn btn-outlined" onClick={handleSubmit}>
                  Login
                </a>
              )}
            </div>
          </div>
        </div>
      </form>
    </div>
  );
}
